import React, { Fragment, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
// import { cacheAdapterEnhancer } from 'axios-extensions';

import { setupCache } from 'axios-cache-adapter'
 
// Create `axios-cache-adapter` instance
// const cache = setupCache({
//   maxAge: 0,
// })

const api = axios.create({
  baseURL: 'https://tv-v2.api-fetch.website/',
  headers: {
    // 'Access-Control-Allow-Origin': '*',
    // 'Access-Control-Allow-Headers': '*',
    'Cache-Control': 'no-cache',
    // 'Host': 'tv-v2.api-fetch.website',
  },
  // params: {
  //   ID: 12345
  // },
  // adapter: cache.adapter,
  // ignoreCache: true,
})

const App = () => {

  const [data, setData] = useState({});

  const getMovie = () => api.get('random/movie').then(res => setData(res.data));

  useEffect(() => {
    getMovie();
  }, []);

  console.log(data);
  axios('https://tv-v2.api-fetch.website/random/movie').then(res => console.log(res));

  return (
    <Fragment>
      <button onClick={getMovie}>Random</button>
      {data.title &&
        <Fragment>
          <h1>{data.title}</h1>
          <p>{data.synopsis}</p>
        </Fragment>
      }
    </Fragment>
  )
}

ReactDOM.render(<App />, document.getElementById('root'));